import React from 'react';
import $ from 'jquery';
import Item from './Item';
import { v4 } from 'uuid';

class DropArea extends React.Component {
    constructor() {
        super()
        this.state = {
            hovered: false,
            files: [],
        }

        $('body').on('ondrop', (e) => {
            e.preventDefault();
        })

        //Add a listener for the browser window.
        window.addEventListener('dragover', this.handleWindowListeners);

        this.onDragEnter = this.onDragEnter.bind(this);
        this.onDragExit = this.onDragExit.bind(this);
        this.onDrop = this.onDrop.bind(this);
        this.removeItem = this.removeItem.bind(this);
    }

    //Removes the default action of browsers when a file is dropped.
    handleWindowListeners(event) {
        event.preventDefault();
    }

    onDragEnter(event) {
        event.preventDefault();
        event.stopPropagation();
        $(".drop-area").addClass('drag-on');
        console.log('drag begin');
    }

    onDragExit(event) {
        event.preventDefault();
        event.stopPropagation();
        $(".drop-area").removeClass('drag-on');
        console.log('drag end');
    }

    onDrop(event) {
        event.preventDefault();
        event.stopPropagation();

        //Create items.
        for (let i = 0; i < event.dataTransfer.files.length; i++) {
            const file = event.dataTransfer.files[i];
            const key = v4();
            this.state.files.push(<Item name={file.name} size={file.size} index={i} onRemove={this.removeItem} id={key} key={key} />);
        }

        //Update state
        this.setState({
            files: this.state.files,
        });
    }

    removeItem(id) {

        //For loop could be used but meh.
        let counter = 0;
        this.state.files.forEach(i => {
            // console.log(`Caller: ${id} -- Looper: ${i.id}`)
            if (id === i.props.id) {
                this.state.files.splice(counter, 1);
            }
            counter++;
        });

        //Update state
        this.setState({
            files: this.state.files,
        });
    }

    render() {
        return (
            <React.Fragment>
                <div className="drop-area" onDragEnter={this.onDragEnter} onDragLeave={this.onDragExit} onDropCapture={this.onDrop} onDrop={this.onDrop}>
                    <div>
                        {this.state.files}
                    </div>
                </div>
                <button>Upload</button>
                <button>Remove All</button>
            </React.Fragment>
        );
    }
}

export default DropArea;