import React from 'react';
import '../Styles/Item.css';

class Item extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            itemName: this.props.name,
            itemSize: this.props.size,
        }

        this.onRemove = this.onRemove.bind(this);
        this.nodeRef = React.createRef();
    }

    onRemove() {
        this.props.onRemove(this.props.id);
    }

    render() {
        return (
            <div ref={this.nodeRef} className="item" >
                <span className="item-name">{this.state.itemName}</span>
                <span className="item-size">{this.state.itemSize}</span>
                <button className="item-remove" onClick={this.onRemove}>Remove</button>
            </div>
        );
    }
}

export default Item;